import * as React from 'react';
import { Typography } from '@mui/material';
import '../../CSS/Analysis.css';

function InterviewSynopsis() {
    return (
        <>
        <Typography variant='body2' color="textPrimary" align="left"> 
            <p>
                Synopsis of interviews with different user classes:
                <ol>
                    <li>
                        We interviewed a boy who is new to board gaming. In the interview, it became clear that his primary goals revolved around finding games that suited his preferences, exploring the world of board gaming, and improving his skills for game nights with friends. His main frustrations resulted from the financial commitment and uncertainty of buying games he might not enjoy. He sought a solution that would allow him to try out different games without the burden of purchasing them.
                        <br></br>The concept of a board game rental website greatly appealed to him, as it would provide a convenient and cost-effective way to explore various board games. His concern about feeling like a novice during game nights with friends highlighted his desire to be well-prepared and confident. His main concerns centered on user-friendliness, convenience, and beginner-friendliness in board games, indicating a need for accessible and easy-to-grasp gaming experiences.
                    </li>
                    <li>
                        We interviewed a mid-aged man who is married and has two children. He was determined to enhance the family's bonding experience and draw his two children, aged 8 and 10, away from the captivating screens that often consume their attention. While he recognizes the educational and recreational value of board games, he faces several challenges in buying them. The costs can quickly add up, making it financially impractical to continuously purchase new games. Furthermore, predicting which games will connect with his children is uncertain, and the clutter of accumulated games poses a storage challenge. In the interview, he highlights these obstacles, emphasizing the need for a more cost-effective, diverse, and convenient solution to introduce his family to the joys of board gaming and quality family time.
                    </li>
                    <li>
                        We interviewed a teenage girl who really loves playing board games. In the interview, her passion for board gaming and her desire to host budget-friendly game nights with friends, explore new board games, and create vlogs of her gaming experiences were evident. Her primary frustration was the affordability of board games, given her limited pocket money. She expressed a strong desire to afford more games within her budget and find new games to enrich her gaming experiences. The interview revealed an opportunity to address these frustrations and needs on the board game rental website.
                    </li>
                </ol>
               
                Key insights from the interviews: 
                <ul>
                    <li>User-friendliness, convenience, and beginner-friendliness in board games are a few main concerns, indicating a need for accessible and easy-to-grasp gaming experiences.</li>
                    <li>Importance of ensuring an enjoyable experience while simultaneously adhering to a budget-friendly approach.</li>
                    <li>There is a strong desire to uncover a variety of board games that yet again aligns with a budget-friendly approach.</li>
                </ul>
            </p>
        </Typography>
        </>
    )
}

export default InterviewSynopsis;